/* hero section css start */
.hero {
  padding-top: rem(200px);
  padding-bottom: rem(160px);
  position: relative;
  z-index: 1;
  @include media(991px) {
    padding-top: rem(150px);
    padding-bottom: rem(120px); }
  @include media(767px) {
    padding-top: rem(120px);
    padding-bottom: rem(80px); }
  &::before {
    @include positionTwo;
    @extend %base-color-two;
    opacity: 0.45;
    z-index: -2;
    @include media(991px) {
      opacity: 0.65; } }
  &__top-title {
    font-size: rem(20px);
    @extend %text--base;
    text-decoration: underline;
    @include media(1199px) {
      font-size: rem(18px); }
    @include media(991px) {
      font-size: rem(16px); } }
  &__title {
    font-size: rem(72px);
    font-weight: 700;
    line-height: 1.2;
    @include media(1399px) {
      font-size: rem(62px); }
    @include media(1199px) {
      font-size: rem(56px); }
    @include media(991px) {
      font-size: rem(42px); }
    @include media(575px) {
      font-size: rem(36px); } }
  &__description {
    font-size: rem(20px);
    margin-top: rem(15px);
    @include media(1199px) {
      font-size: rem(18px); }
    @include media(991px) {
      font-size: rem(16px); } }
  &__radar {
    position: absolute;
    top: 22%;
    right: 10%;
    width: 500px;
    height: 500px;
    z-index: -1;
    @include media(991px) {
      width: 400px;
      height: 400px;
      right: 50%;
      margin-right: -200px; }
    @include media(767px) {
      width: 300px;
      height: 300px;
      margin-right: -150px; }
    .radar {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100%;
      animation: radarRotate 10s infinite linear; }
    [class*="dot-"] {
      position: absolute;
      width: 10px;
      height: 10px;
      @extend %base-color;
      @include border-radius(50%);
      z-index: -1;
      @include animation(pulseFlow 2000ms linear infinite);
      &::before, &::after {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        border-radius: 74px;
        @extend %base-color;
        opacity: 0.15;
        z-index: -1; }
      &::before {
        z-index: -1;
        @include animation(inner-ripple 2000ms linear infinite); }
      &::after {
        z-index: -1;
        @include animation(outer-ripple 2000ms linear infinite); } }
    .dot-1 {
      top: 39%;
      left: 17%;
      &::before, &::after {
        animation-delay: 0.3s; } }
    .dot-2 {
      bottom: 27%;
      left: 50%;
      &::before, &::after {
        animation-delay: 0.5s; } }
    .dot-3 {
      top: 32%;
      right: 35%;
      &::before, &::after {
        animation-delay: 0.1s; } }
    .dot-4 {
      top: 15%;
      left: 50%; }
    .dot-5 {
      bottom: 18%;
      left: 25%;
      &::before, &::after {
        animation-delay: 0.7s; } }
    .dot-6 {
      bottom: 37%;
      right: 15%; } } }
/* hero section css end */


/* inner hero css start */
.inner-hero {
  padding-top: rem(120px);
  padding-bottom: rem(50px);
  position: relative;
  z-index: 1;
  position: relative;
  z-index: 1;
  &::before {
    @include positionTwo;
    @extend %base-color-two;
    opacity: 0.75;
    z-index: -1; } }


.page-breadcrumb {
  @include d-flex;
  margin-top: rem(5px);
  li {
    color: rgba(#ffffff, 0.8);
    text-transform: capitalize;
    &::after {
      content: '-';
      color: #ffffff;
      margin: 0 rem(5px); }
    &:first-child {
      &::before {
        content: "\f015";
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: $base-color;
        margin-right: rem(6px); } }
    &:last-child {
      &::after {
        display: none; } }
    a {
      color: #ffffff;
      text-transform: capitalize;
      &:hover {
        color: $base-color; } } } }
/* inner hero css end */
