/* about section css start */
.about-list {
  li {
    &::before {
      @include media(991px) {
        position: static;
        font-size: rem(20px); } } } }
/* about section css end */


/* how it work css start */
.work-item {
  position: relative;
  &:nth-child(2) {
    &::before, &::after {
      @include media(991px) {
        display: none; } } }
  &:last-child {
    &::before, &::after {
      display: none; } }
  &::after {
    position: absolute;
    content: '';
    top: 80px;
    right: -50px;
    width: 100px;
    height: 1px;
    background-color: rgba(#fff, 0.15);
    @include media(1199px) {
      top: 80px;
      right: -30px;
      width: 65px; }
    @include media(575px) {
      display: none; } }
  &::before {
    position: absolute;
    content: "\f105";
    top: 68px;
    right: -56px;
    font-family: 'Line Awesome Free';
    font-weight: 900;
    color: rgba(#fff, 0.15);
    @include media(1199px) {
      top: 68px;
      right: -36px; }
    @include media(575px) {
      display: none; } } }
.work-card {
  text-align: center;
  &__thumb {
    img {
      max-height: rem(180px);
      max-width: rem(230px);
      @include media(1199px) {
        max-width: rem(180px); } } }
  &__content {
    margin-top: rem(30px);
    max-width: 60%;
    margin-left: auto;
    margin-right: auto;
    @include media(1199px) {
      max-width: 80%; } } }
/* how it work css end */


/* why choose section css start */
.choose-card {
  background-color: rgba($base-color-two, 0.15);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  @extend %bs-8;
  @extend %d-flex;
  align-items: center;
  position: relative;
  z-index: 1;
  padding: rem(20px);
  border: 1px solid rgba(#fff, 0.25);
  @extend %transition;
  height: 100%;
  &:hover {
    border-color: $base-color;
    box-shadow: 0 5px 15px rgba($base-color, 0.35); }
  &__icon {
    width: rem(50px);
    height: rem(50px);
    display: flex;
    justify-content: center;
    align-items: center;
    @extend %bs-5;
    @extend %base-color;
    i {
      font-size: rem(32px); } }
  &__title {
    width: calc(100% - 50px);
    padding-left: rem(15px); }
  &__content {
    width: 100%;
    margin-top: rem(20px); } }
/* why choose section css end */

/* overview section css start */
.overview-card {
  text-align: center;
  padding: rem(30px) rem(20px);
  background-color: darken($base-color-two, 3%);
  @extend %bs-8;
  position: relative;
  z-index: 1;
  &:hover {
    &::after {
      top: 0;
      left: 0;
      z-index: -1; } }
  &::after {
    position: absolute;
    content: '';
    top: 10px;
    left: 10px;
    width: 100%;
    height: 100%;
    background-color: transparent;
    border: 1px solid $base-color;
    @extend %bs-8;
    z-index: -2;
    @extend %transition;
    @include media(575px) {
      top: 0;
      left: 0; } }
  &::before {
    @include positionTwo;
    background-color: darken($base-color-two, 2%);
    z-index: -1;
    @extend %bs-8; }
  &__icon {
    i {
      font-size: rem(48px); } }
  &__content {
    margin-top: rem(15px); }
  &__number {
    font-size: rem(36px); }
  &__caption {
    color: $para-color; } }

.overview-item {
  &:nth-of-type(4n + 1) {
    .overview-card__icon {
      i {
        color: #fd4766; } }
    .overview-card {
      &::after {
        border-color: #fd4766; } } }
  &:nth-of-type(4n + 2) {
    .overview-card__icon {
      i {
        color: #337dff; } }
    .overview-card {
      &::after {
        border-color: #337dff; } } }
  &:nth-of-type(4n + 3) {
    .overview-card__icon {
      i {
        color: #ff8f3c; } }
    .overview-card {
      &::after {
        border-color: #ff8f3c; } } }
  &:nth-of-type(4n + 4) {
    .overview-card__icon {
      i {
        color: #3eb75e; } }
    .overview-card {
      &::after {
        border-color: #3eb75e; } } } }
/* overview section css end */

/* packaage section css start */
.package-card {
  padding: rem(25px);
  @extend %base-color-two;
  border: 2px solid rgba(#fff, 0.25);
  text-align: center;
  @extend %bs-8;
  &__name {
    font-size: rem(22px);
    @include media(575px) {
      font-size: rem(20px); } }
  &__price {
    font-size: rem(56px);
    font-weight: 700;
    font-family: $heading-font;
    line-height: 1.2;
    @include media(575px) {
      font-size: rem(48px); } }
  &__feature-list {
    text-align: left;
    display: inline-block;
    li {
      position: relative;
      padding: rem(10px) rem(10px) rem(10px) rem(30px);
      &::before {
        position: absolute;
        content: "\f00c";
        top: 14px;
        left: 0;
        font-family: 'Line Awesome Free';
        font-weight: 900;
        width: 20px;
        height: 20px;
        @extend %base-color;
        @extend %text--white;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: rem(11px);
        @include border-radius(50%); } } }
  &.popular-package {
    padding-top: rem(65px);
    position: relative;
    &::after {
      position: absolute;
      content: 'Most Popular';
      @extend %base-color;
      @extend %text--white;
      top: 15px;
      right: -10px;
      font-size: rem(14px);
      padding: 0 rem(15px); }
    &::before {
      position: absolute;
      content: '';
      top: 38px;
      right: -20px;
      border-width: 0px 10px 11px 10px;
      border-style: solid;
      border-color: transparent transparent transparent $base-color; } } }
/* packaage section css end */


/* testimonial section css start */
.testimonial-card {
  padding: rem(20px);
  background-color: rgba($base-color-two, 0.55);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  @extend %bs-8;
  @extend %d-flex;
  border: 1px solid rgba(#fff, 0.25);
  @extend %transition;
  @include media(420px) {
    justify-content: center; }
  &__thumb {
    width: rem(65px);
    height: rem(65px);
    @include border-radius(50%);
    overflow: hidden; }
  &__content {
    width: calc(100% - 65px);
    padding-left: rem(20px);
    @include media(420px) {
      width: 100%;
      padding-left: 0;
      text-align: center;
      margin-top: rem(20px); } }
  &__description {
    font-style: italic;
    color: $para-color; } }
.ratings {
  i {
    font-size: rem(14px);
    color: #FEC42D; } }

.testimonial-slider {
  .slick-list {
    margin: 0 rem(-10px); }
  .single-slide {
    padding: 0 rem(10px); } }

.text-white-75 {
  color: $para-color; }
/* testimonial section css end */

/* blog section css start */
.blog-post {
  padding: rem(15px);
  @extend %bs-8;
  &__thumb {
    height: rem(220px);
    @extend %bs-5;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%; } }
  &__content {
    padding: rem(20px) 0 0 0; }
  &__title {
    a {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis; } } }
/* blog section css end */

/* blog details section css start */
.blog-details-title {
  font-size: rem(42px);
  @include media(1399px) {
    font-size: rem(36px); }
  @include media(991px) {
    font-size: rem(32px); }
  @include media(575px) {
    font-size: rem(28px); } }

.post-share {
  padding: rem(20px) 0;
  border-top: 1px solid rgba(#fff, 0.15);
  border-bottom: 1px solid rgba(#fff, 0.15);
  margin: rem(-3px) rem(-5px);
  li {
    padding: rem(3px) rem(5px);
    a {
      width: rem(35px);
      height: rem(35px);
      border: 1px solid rgba(#fff, 0.15);
      @include border-radius(50%);
      display: flex;
      justify-content: center;
      align-items: center;
      color: $para-color;
      &:hover {
        @extend %base-color;
        @extend %text--white; } } } }

.blog-sidebar {
  padding: rem(20px);
  .title {
    position: relative;
    padding-bottom: rem(5px);
    margin-bottom: rem(30px);
    &::after {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: rem(35px);
      height: rem(2px);
      @extend %base-color; } } }

.s-post {
  padding: rem(15px) 0;
  border-bottom: 1px solid rgba(#fff, 0.15);
  &:first-child {
    padding-top: 0; }
  &:last-child {
    padding-bottom: 0;
    border-bottom: none !important; }
  &__thumb {
    width: rem(80px);
    height: rem(70px);
    img {
      width: 100%;
      height: 100%;
      @extend %obj-fit; } }
  &__content {
    width: calc(100% - 80px);
    padding-left: rem(20px); }
  &__title {
    font-size: rem(16px);
    a {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis; } } }
/* blog details section css end */

/* subscribe section css start */
.subscribe-section {
  padding: rem(50px) 0; }
.subscribe-form {
  display: flex;
  flex-wrap: wrap;
  .form--control {
    background-color: transparent;
    border-color: rgba(#fff, 0.15);
    border-right: none;
    color: #fff;
    @include placeholder(darken(#fff, 20%));
    @include media(420px) {
      border-right: 1px solid rgba(#fff, 0.15); } }
  .custom-icon-field {
    flex-grow: 4;
    @include media(420px) {
      width: 100%; } }
  .subscribe-btn {
    flex-grow: 1;
    @extend %base-color;
    @extend %text--white;
    @include border-radius(0 6px 6px 0);
    @include media(420px) {
      width: 100%;
      @include border-radius(6px);
      padding: rem(10px) rem(15px);
      margin-top: rem(10px); } } }
/* subscribe section css end */
