/* login modal css start */
.login-wrapper {
  padding: rem(30px);
  border: 2px solid rgba(#fff, 0.15);
  &__icon {
    width: 85px;
    height: 85px;
    @include border-radius(50%);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: rem(48px); } }

.login-modal {
  .btn-close {
    position: absolute;
    width: 30px;
    height: 30px;
    background-color: #ffffff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    opacity: 1;
    right: -12px;
    top: -15px; } }
/* login modal css end */

/* registration section css start */
.registration-section {
  position: relative;
  z-index: 1;
  [class*="el-"] {
    position: absolute;
    z-index: -1; }
  .el-1 {
    bottom: 0;
    left: 0;
    img {
      max-width: 450px;
      @include media(1750px) {
        max-width: 350px; }
      @include media(1550px) {
        max-width: 250px; } } }
  .el-2 {
    bottom: 0;
    right: 0;
    img {
      max-width: 450px;
      @include media(1750px) {
        max-width: 350px; }
      @include media(1550px) {
        max-width: 250px; } } } }
.registration-wrapper {
  padding: 40px;
  @include border-radius(10px);
  border: 2px solid rgba(#fff, 0.15);
  @include media(575px) {
    padding: rem(20px); } }
/* registration section css end */
