/* hero radar animation css start */
@include keyframes(radarRotate) {
  0% {
    @include transform(translate(-50%, -50%) rotate(0deg)); }
  100% {
    @include transform(translate(-50%, -50%) rotate(360deg)); } }

@include keyframes(pulseFlow) {
  0% {
    @include transform(scale(1)); }
  30% {
    @include transform(scale(0.6)); }
  100% {
    @include transform(scale(1)); } }

@include keyframes(outer-ripple) {
  0% {
    @include transform(scale(1));
    opacity: 0.5; }
  80% {
    @include transform(scale(1.5));
    opacity: 0.8; }
  100% {
    @include transform(scale(2.5));
    opacity: 0; } }

@include keyframes(inner-ripple) {
  0% {
    @include transform(scale(1));
    opacity: 0.5; }
  30% {
    @include transform(scale(1));
    opacity: 0.5; }
  100% {
    @include transform(scale(1.5));
    opacity: 0; } }
/* hero radar animation css end */
