/* dashboard section css start */
.d-widget {
  padding: rem(20px);
  @include border-radius(8px);
  // position: relative
  @extend %base-color-two;
  z-index: 1;
  @extend %d-flex;
  align-items: center;
  // margin-top: 16px
  // &::before
  //   position: absolute
  //   content: ''
  //   top: -10px
  //   left: 3px
  //   width: 75px
  //   height: 30px
  //   @extend %base-color-two
  //   z-index: -1
  //   clip-path: polygon(0 0, 80% 0, 100% 100%, 0% 100%);
  // &::after
  //   position: absolute
  //   content: ''
  //   top: -16px
  //   left: 0
  //   width: 63px
  //   height: 30px
  //   @extend %base-color-two
  //   +border-radius(8px)
  //   z-index: -1
  &__icon {
    width: 90px;
    text-align: center;
    position: relative;
    z-index: 1;
    @include media(767px) {
      width: 60px; }
    &::after {
      position: absolute;
      content: '';
      top: 50%;
      left: 50%;
      width: 30px;
      height: 30px;
      @include border-radius(50%);
      @extend %base-color;
      opacity: 0.45;
      z-index: -1;
      margin-top: -7px;
      margin-left: -7px;
      @include media(767px) {
        width: 20px;
        height: 20px;
        margin-top: -3px;
        margin-left: -3px; } }
    i {
      font-size: rem(38px);
      @include media(767px) {
        font-size: rem(32px); } } }
  &__content {
    width: calc(100% - 90px);
    padding-left: rem(20px);
    border-left: 1px solid rgba(#fff, 0.15); } }
/* dashboard section css end */
