body.lightmode {
  background-color: #fff;
  color: #505050;
  .choose-card__content {
    p {
      color: #e4e4e4; } }
  h1, h2, h3, h4, h5, h6, h1>a, h2>a, h3>a, h4>a, h5>a, h6>a {
    color: #17173A; }
  .package-card {
    background-color: #fff;
    border-color: #96969640;
    box-shadow: 0 5px 25px rgba(#000, 0.1); }
  .custom--accordion {
    .accordion-item {
      border-color: rgba(#000, 0.15)!important; }
    .accordion-button {
      background-color: darken(#fff, 5%);
      color: #17173A; } }
  .section--bg {
    background-color: #F2F2F5; }
  .custom--accordion .accordion-button::after {
    color: #777; }
  .subscribe-form .form--control {
    border-color: rgba(#000, 0.15);
    color: #363636;
    @include placeholder(rgba(#000, 0.65)); }
  .work-item::before {
    color: rgba(#000, 0.15); }
  .work-item::after {
    background-color: rgba(#000, 0.15); }
  .pagination {
    .page-item {
      .page-link {
        background-color: #eaeaea;
        color: #363636; } } }
  .post-share {
    border-color: #e5e5e5;
    li {
      a {
        border-color: #e5e5e5;
        color: #505050;
        &:hover {
          color: #fff; } } } }
  .s-post {
    border-color: #e5e5e5; }
  label, .contact-info-list .contact-info-single a {
    color: #505050; }
  .transparent-form .form--control {
    border-color: #c7c7c7; }
  .contact-wrapper__right {
    background-color: #e8e8e8; }
  .contact-social-links li a {
    color: #363636; }
  .form--control {
    @include placeholder(darken(#fff, 50%));
    color: #363636; }
  .d-widget {
    background-color: #F2F2F5;
    .d-widget__content {
      border-left-color: #d6d6d6; } }
  .custom--card {
    background-color: #fff;
    tbody {
      td {
        color: #777;
        border-bottom-color: #ececec; } } }
  [data-label]::before {
    color: #363636; }
  .table-responsive--md tbody tr:nth-child(odd) {
    @include media(991px) {
      background-color: #f1f1f1; } }
  .table-responsive--sm tbody tr:nth-child(odd) {
    @include media(767px) {
      background-color: #f1f1f1; } } }

