/* contact section start */
.map-area {
  padding-top: rem(150px);
  padding-bottom: rem(200px);
  position: relative;
  z-index: 1;
  @include media(991px) {
    padding-top: rem(80px);
    padding-bottom: rem(100px); }
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 145%;
    height: 100%;
    background: $base-color;
    background: radial-gradient(circle, rgba(0,212,255,0) 10%, $base-color-two 15%, $base-color-two 100%, $base-color 80%);
    z-index: -1;
    opacity: 0.75;
    @include media(991px) {
      width: 100%; } } }

.map-info-box {
  position: relative;
  width: 100%;
  height: 200px;
  margin-left: auto;
  &:hover {
    .map-info {
      opacity: 1;
      visibility: visible;
      bottom: 75%;
      @include media(991px) {
        bottom: 97%; } } }
  .map-info {
    position: absolute;
    width: 300px;
    text-align: left;
    padding: 20px;
    @extend %base-color-two;
    right: 0;
    bottom: 70%;
    opacity: 0;
    visibility: hidden;
    @extend %transition;
    @include media(1540px) {
      right: 50px; }
    @include media(991px) {
      right: 45%; }
    @include media(575px) {
      right: auto;
      left: 50%;
      @include transform(translateX(-50%)); }
    &::after {
      position: absolute;
      content: '';
      bottom: -22px;
      right: 38px;
      width: 25px;
      height: 30px;
      @extend %base-color-two;
      clip-path: polygon(100% 0, 0 0, 100% 100%);
      @include media(575px) {
        right: 50%; } } } }
.map-icon {
  width: 75px;
  height: 75px;
  display: inline-block;
  position: absolute;
  top: 70px;
  right: 0;
  @include media(1540px) {
    right: 50px; }
  @include media(991px) {
    top: 30px;
    right: 45%; }
  i {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 72px;
    @extend %text--base;
    -webkit-animation: topBottom 2000ms ease-in-out infinite;
    animation: topBottom 2000ms ease-in-out infinite;
    @include media(1199px) {
      font-size: 56px; } } }
@include keyframes(topBottom) {
  from {
    transform: translate(0, 0.3vw); }
  70% {
    transform: translate(0, 0vw); }
  to {
    transform: translate(0, 0.3vw); } }

.map-pointer-shadow {
  position: absolute;
  bottom: -15px;
  right: -5px;
  height: 1.5vw;
  width: 4.5vw;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  -webkit-animation: pulse 2000ms ease-in-out infinite;
  animation: sahdowPulse 2000ms ease-in-out infinite; }

@include keyframes(sahdowPulse) {
  from {
    transform: scale(1, 1);
    opacity: 1; }
  70% {
    transform: scale(0.8, 0.8);
    opacity: 0.7; }
  to {
    transform: scale(1, 1);
    opacity: 1; } }

.contact-area {
  margin-top: rem(-100px);
  position: relative;
  z-index: 3; }
.contact-wrapper {
  border: 1px solid rgba(#fff, 0.15);
  @include border-radius(8px);
  &__left {
    width: 65%;
    padding: rem(40px);
    @include media(1199px) {
      width: 55%; }
    @include media(767px) {
      width: 100%; }
    @include media(480px) {
      padding: rem(20px); } }
  &__right {
    width: 35%;
    padding: rem(40px);
    background-color: darken($base-color-two, 1%);
    display: flex;
    flex-wrap: wrap;
    flex-flow: column;
    justify-content: space-between;
    @include media(1199px) {
      width: 45%; }
    @include media(767px) {
      width: 100%; }
    @include media(480px) {
      padding: rem(20px); } } }

.contact-info-list {
  .contact-info-single + .contact-info-single {
    margin-top: 20px; }
  .contact-info-single {
    @extend %d-flex;
    align-items: center;
    i {
      font-size: rem(28px);
      line-height: 1;
      width: 40px;
      @extend %text--base; }
    p {
      width: calc(100% - 50px); }
    a {
      color: $para-color;
      &:hover {
        @extend %text--base; } } } }

.contact-social-links {
  li {
    width: 35px;
    height: 35px;
    position: relative;
    z-index: 1;
    @extend %transition;
    line-height: 38px;
    text-align: center;
    &:hover {
      &::before {
        width: 100%;
        height: 100%; } }
    &::before {
      position: absolute;
      content: '';
      width: 0;
      height: 0;
      top: 50%;
      left: 50%;
      @include transform(translate(-50%, -50%));
      @extend %base-color;
      @include border-radius(50%);
      z-index: -1;
      @extend %transition; }
    a {
      color: $para-color;
      font-size: rem(18px); } } }
/* contact section end */
