/* footer section css start */
.footer {
  @extend %base-color-two;
  padding: rem(30px) 0;
  border-top: 1px solid rgba(#fff, 0.15); }
.footer-logo {
  img {
    max-width: rem(190px);
    max-height: rem(55px); } }
.social-link-list {
  margin: rem(-3px) rem(-5px);
  li {
    padding: rem(3px) rem(5px);
    a {
      width: rem(40px);
      height: rem(40px);
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      @include border-radius(50%);
      border: 1px solid rgba(#fff, 0.15);
      @extend %transition;
      &:hover {
        @extend %base-color;
        border-color: transparent; } } } }
.inline-menu {
  margin: rem(-4px) rem(-15px);
  li {
    padding: rem(4px) rem(15px);
    position: relative;
    &:last-child {
      &::after {
        display: none; } }
    &::after {
      position: absolute;
      content: '';
      top: 50%;
      right: 0;
      margin-top: -2px;
      margin-right: -2px;
      width: rem(5px);
      height: rem(5px);
      @include border-radius(50%);
      background-color: rgba(#fff, 0.25); }
    a {
      color: #fff;
      font-size: rem(15px);
      @extend %transition;
      &:hover {
        @extend %text--base; } } } }
/* footer section css end */
